<template>
  <div class="login">
    <!-- 头部 -->
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />
    
    <!-- 主体 -->
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="请输入手机号码" type="text">
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="5" placeholder="请输入图形验证码" type="text">
          <img v-if="picUrl" :src="picUrl" @click="getPicCode" alt="">
        </div>
        <div class="form-item">
          <input v-model="msgCode" class="inp" placeholder="请输入短信验证码" type="text">
          <button @click="getCode">
            {{  second === totalScend ? '获取验证码' : second + '秒后重新发送' }}
          </button>
        </div>
      </div>

      <div @click="mobileLogin" class="login-btn">登录</div>
    </div>
  </div>
</template>

<script>
import { codeLogin, getMsgCode, getPicCode } from '@/api/login'

export default {
  name: 'LoginPage',
  async created () {
    this.getPicCode()
  },
  data () {
    return {
      picCode: '', // 用户输入的图形验证码
      picKey: '', // 图形唯一标识
      picUrl: '',
      totalScend: 60, // 总mian'shu总秒数
      second: 60, // 当前秒数
      timer: null, // 定时器id
      mobile: '',  // 手机号
      msgCode: ''  // 短信验证码
    }
  },
  methods: {
    // 获取图形验证码
    async getPicCode () {
      const { data : { base64, key } } = await getPicCode() // 此处函数为api中封装的函数，而非递归
      // 将 res 解构
      // res => { data } 
      // const res = await request.get('/captcha/imag')
      // console.log(res)
      this.picUrl = base64
      this.picKey = key

      // Toast => 提示消息
      this.$toast('获取成功')
    },

    // 校验  手机号和验证码是否合规
    //   正则表达式： /^1[3-9]\d{9}$/  以1开头，第二位为3-9，后面9位数字
    //   /^   $/    ^ => 以...开头， $ => 以...结尾
    //   \d  => 数字    \w  => 字符
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('请输入正确的验证码')
        return false
      }
      return true
    },

    // 获取短信验证码
    getCode () {
      // 未通过校验，不继续执行
      if (!this.validFn()) {
        return
      }

      if ( !this.timer && this.second === this.totalScend ){
        // 发送请求获取验证码
        getMsgCode(this.picCode, this.picKey, this.mobile)
        // console.log(res)
        this.$toast('短信发送成功，注意查收')
      
        // 开定时器倒计时
        this.timer = setInterval(() => {
        this.second--
        if ( this.second < 1 ) {
          clearInterval(this.timer)
          this.timer = null
          this.second = 60
          }
        }, 1000)

        // getPicCode()
        this.$toast('发送成功，注意查收')
        }

      
      },
      async mobileLogin() {
        if (!this.validFn()) {
          return
        }
        if (!/^\d{6}$/.test(this.msgCode)) {
          this.$toast('请输入正确的验证码')
          return
        }
        const res = await codeLogin(this.mobile, this.msgCode) 
        this.$store.commit('user/setUserInfo',res.data)
        console.log(res)
        if (res.status === 200 ) {
          this.$toast('登陆成功')

      // 如果有返回跳转的参数，则跳转，否则跳转回主页
          const url = this.$route.query.backUrl || '/'
          this.$router.replace(url)
        }
      }
  },
  // 离开页面后，清除 定时器
  destroyed () {
    clearInterval(this.timer)
  }
}


</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>